import React, { useState, useEffect } from 'react';
//

async function signup(credentials) {
    try {
        return fetch('http://192.168.31.224:8081/api/auth/signup', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Content-Length': '999999'
        },
        body: JSON.stringify(credentials)
    })
    .then(data => data.json())
    } catch (error) {
        console.log('erorka', error)
    }
    
}


let SignUpForm = ({ registerWindow, setRegisterWindow }) => {
// state
    const [username, setUserName] = useState();
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();

    const [data, setData] = useState()
// form submit
    const handleSubmit = async e => {
        e.preventDefault();
        const token = await signup({
            username,
            email,
            password
        });
        setData(token);
    }       

    console.log('password', password)

    return (
        <div className='background_auth'>
            <div className="background">
                <div className="shape"></div>
                <div className="shape"></div>
            </div>

            <form onSubmit={handleSubmit}>
                <div className='pb-20 flex-row flex-jcsb flex-align-items-center'>
                    <h3>Registration</h3>

                    <div onClick={() => setRegisterWindow(!registerWindow)} className="close">
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                        <svg viewBox="0 0 36 36" className="circle">
                            <path
                            stroke-dasharray="100, 100"
                            d="M18 2.0845
                                a 15.9155 15.9155 0 0 1 0 31.831
                                a 15.9155 15.9155 0 0 1 0 -31.831"
                            />
                        </svg>
                    </div>
                </div>
            
                <input type="text" placeholder="Username"
                    onChange={e => setUserName(e.target.value)} />

                <input type="email" placeholder="Email Address"
                    onChange={e => setEmail(e.target.value)} />

                <input type="password" placeholder="Password"
                    onChange={e => setPassword(e.target.value)} />

               

                {/* <input type="tel" placeholder="Phone Number" 
                    onChange={e => setPassword(e.target.value)} /> */}

                <button>Register</button>
            </form>
        </div>
    )
} 



export default SignUpForm