import React, { useState } from 'react'; import ReactDOM from 'react-dom'; 
import { IntlProvider } from 'react-intl'

import { LOCALES } from './UiHelpers/i18n/locales'
import { messages } from './content/main_page/content/main_page_header/headerTextMessage'

import { HashRouter } from 'react-router-dom'; 
import '~CSS/main.sass';

import { BasicContext } from './Context'

// ------------------------->
// Main components + helpers modules. 


const App_Index = ( ) => {

  const [currentLocale, setCurrentLocale] = useState(LOCALES.POLISH)

  const handleChange = ({ target: { value } }) => {
      setCurrentLocale(value)
  }

  console.log('currendastLocale', currentLocale)

  return (
    <>

      <IntlProvider 
        messages={messages[currentLocale]}
        locale={currentLocale}
        defaultLocale={LOCALES.POLISH}
      >
        <HashRouter>
          <BasicContext currentLocale={currentLocale} handleChange={handleChange}>
            {/* Child */}
          </BasicContext>
        </HashRouter>
      </IntlProvider>

  </>
  )
}

ReactDOM.render(
  <App_Index />
, document.getElementById('vas')
);