import React, { useCallback, useEffect, useState } from 'react'; 
// Swiper
import Swiper_ from './UiHelpers/swiper'
// Header  -
import HeaderNav from './routes/headerNav/headerNav';
// Content -
import Routes from './routes/Routes'

import Select_lang from './UiHelpers/i18n/select_language'
//
// ------------------>
const App = ({ currentLocale, handleChange }) => {
  
 // 100% window size fix.
  window.addEventListener('resize', () => {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
  });
	
  return (
    <>

      <Select_lang currentLocale={currentLocale} handleChange={handleChange} />
      <Swiper_ />
      {/* <HeaderNav /> */}
      <div className='main'>
        <Routes />
      </div>
    </>
  )
  
}



export default React.memo(App);